@import url(https://fonts.googleapis.com/css?family=Raleway:700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* SIGN CSS */
.page {
    display: grid;
    grid-template-rows: 33% 33% 33%;
    grid-row-gap: 30%;
    row-gap: 30%;
    justify-content: center;
    align-items: center;
    margin-top: 5%;
    width: 90%;
}

#signs img {
    width: 100%;
    -webkit-animation: rotate 60s ease-in-out 0s infinite;
            animation: rotate 60s ease-in-out 0s infinite;
    border-radius: 50%;
}

@-webkit-keyframes rotate {
    to {
        rotate: 360deg;
    }

    from {
        rotate: 0deg;
    }
}

@keyframes rotate {
    to {
        rotate: 360deg;
    }

    from {
        rotate: 0deg;
    }
}

#sign {
    padding: 1%;
    border: none;
    background: transparent;
    border-bottom: 1px solid darkorchid;
    color: linear-gradient(to left, #ccc, #333, #ccc);
    font-weight: bold;
    font-style: italic;
    font-size: larger;
    margin-bottom: 10%;
}

#sign:focus {
    outline: none;
    color: lightslategray;
}

.page h1 {
    margin-left: 33%;
    color: blueviolet;
    font-style: italic;
    font-weight: bold;

}


/* HOROSCOPE CSS  */
.days {
    display: grid;
    grid-template-columns: repeat(3, 33%);
    padding: 1.2%;
    justify-content: center;
    margin-top: 8%;
}

.days input {
    padding: 5%;
    background-color: white;
    border: none;
    border-bottom: 1px solid gray;
    border-radius: 1%;
    box-shadow: .3px .3px 1px .3px gray;
    margin-inline: 1%;
    text-align: center;
    font-weight: bolder;
    font-size: 30px;
    transition: .3s ease-in-out;
    color: gray;
}

.days input:hover {
    border: blueviolet 1px solid;
    color: blueviolet;
    cursor: pointer;
    box-shadow: 2px 5px blueviolet;
    transform: scale(.99);
}


#app-name {
    color: gray;
    width: 10%;
    text-align: center;
    padding: .5%;
    margin-top: 1%;
    left: 0;
    top: 0;
    position: fixed;
    transition: .1s ease-in-out;
}

#app-name:hover {
    color: lightslategray;
}

.zodiac {
    display: grid;
    grid-template-areas:
        "signs sign sign"
        "day day day"
        "date date date"
        "basics basics horoscope-head"
        "info . horoscope"
    ;
    width: 85%;
    box-shadow: .3px .3px 1px .3px gray;
    padding: 5%;
    background-color: white;
    justify-content: center;
    align-items: center;
    color: gray;
    grid-column-gap: 3%;
    -webkit-column-gap: 3%;
            column-gap: 3%;
    grid-row-gap: 2%;
    row-gap: 2%;
    transition: .3s ease-in-out;
    margin-top: 2%;
}

.zodiac:hover {
    box-shadow: .3px .3px 1px .3px blueviolet;
}

#title {
    grid-area: sign;
    width: auto;
    border-bottom: 1.5px dashed lightslategray;
    color: lightslategray;
}

.the-date {
    grid-area: date;
    display: grid;
    grid-template-columns: 50% 50%;
    width: 25%;
    padding: 2%;
}

#the-day {
    width: auto;
    border-bottom: 1px dashed lightslategray;
    padding: 1%;
}

#date {
    width: auto;
    font-size: larger;
    font-style: oblique;
    border-bottom: 1px dashed lightslategray;
}


#basics {
    grid-area: basics;
}

.info {
    grid-area: info;
    border-right: 1.2px dashed lightslategray;
}

.info li {
    list-style: none;
}

#mood {
    grid-area: mood;
}

#horoscope-head {
    grid-area: horoscope-head;
}

#horoscope {
    grid-area: horoscope;
    width: 75%;
}

#signs-app {
    grid-area: signs;
    width: 60%;
    -webkit-animation: rotate 30s ease-in-out 0s infinite;
            animation: rotate 30s ease-in-out 0s infinite;
}
.footer {
    position: fixed;
    bottom: 0;
    left: 0;
    padding: 1%;
    width: 100%;
}

.footer hr {
    border: 0;
    height: 1px;
    background: #333;
    background-image: linear-gradient(to left, #ccc, #333, #ccc);
}

#footer-list {
    display: grid;
    grid-template-columns: repeat(1, 100%);
    padding: .5%;
}

#footer-links {
    text-decoration: none;
    list-style: none;
    color: gray;
    transition: .2s ease-in;
}

#footer-links:hover {
    color: blueviolet;
    transform: scale(1.03);
}
* {
  margin: auto;
  /* margin-right: 3%; */
  width: 95%;
}

body {
  width: 100%;
  margin-top: auto;
  background: linear-gradient(to bottom, #d5d4d0 0%, #d5d4d0 10%, #eeeeec 31%, #efeeec 75%, #e9e9e7 100%) center center fixed;

}

.App {
  font-family: 'Raleway', sans-serif;

}

@media (min-width: 1400px) {
  body {
    width: 90%;
  }
}
